import * as moment from 'moment';

export interface NewsBodyElement {
  type: string;
  data: any;
}

export class News {
  public title: string;
  public titleDetail?: string;
  public date: moment.Moment;
  public intro: string;
  public body?: NewsBodyElement[];
  public slug?: string;
  public url?: string;
  public images: any;
  public language: string;
}




