import { Component, OnInit } from '@angular/core';
import 'rxjs/add/operator/filter';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { TranslateService, LangChangeEvent } from '@ngx-translate/core';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

  // it is closed by defualt
  public isCollapsedMenu = true;

  public currentLanguage: string;

  private routeData: any;

  constructor(private translate: TranslateService, private router: Router, private route: ActivatedRoute) {
    this.currentLanguage = translate.defaultLang;

    translate.onLangChange.subscribe((event: LangChangeEvent) => {
      this.currentLanguage = event.lang;
    });
  }


  /**
   * Change language
   * @param language is 'en' OR 'hr'
   */
  public changeLanguage(language: string) {
    // 1. change language
    this.translate.use(language);
    this.currentLanguage = language;

    // 2. go to complement state in changed language
    this.router.navigate(['/' + this.routeData.config[language]]);
  }


  ngOnInit() {
    this.router.events
    .filter(event => event instanceof NavigationEnd)
    .subscribe(event => {
      let currentRoute = this.route.root;
      while (currentRoute.children[0] !== undefined) {
        currentRoute = currentRoute.children[0];
      }

      this.routeData = currentRoute.snapshot.data;
    });
  }

}



