import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Parse } from '../../plugins/parse';
import { Promise } from 'parse';

@Injectable()
export class MailService {

  constructor(private http: Http) {

  }


  public send(name: string, from: string, subject: string, message: string, captcha: string): Promise<any> {
    const data = {
      sender: name,
      from: from,
      subject: subject,
      message: message,
      captcha: captcha
    };
    return Parse.Cloud.run('mail', data);
  }

}



