import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import {
  HomeComponent,
  InformationsComponent,
  DocumentsComponent,
  EventsComponent,
  ContactComponent,
  NewsComponent,
  NewsDetailComponent,
  EventDetailComponent,
  DocumentDetailComponent
} from './containers';
import { AppContainersModule } from './containers/app-containers.module';

// Routes configuration
const appRoutes: Routes = [
  {
    path: 'home',
    component: HomeComponent,
    data: {
      language: 'en',
      config: {
        hr: ''
      }
    }
  },
  // hr - Home is default root page
  {
    path: '',
    component: HomeComponent,
    data: {
      language: 'hr',
      config: {
        en: 'home'
      }
    }
  },
  {
    path: 'information',
    component: InformationsComponent,
    data: {
      language: 'en',
      config: {
        hr: 'informacije'
      }
    }
  },
  {
    path: 'informacije',
    component: InformationsComponent,
    data: {
      language: 'hr',
      config: {
        en: 'information'
      }
    }
  },
  {
    path: 'documents',
    component: DocumentsComponent,
    data: {
      language: 'en',
      config: {
        hr: 'dokumenti'
      }
    }
  },
  {
    path: 'documents/:slug',
    component: DocumentDetailComponent,
    data: {
      language: 'en',
      config: {
        hr: 'dokumenti'
      }
    }
  },
  {
    path: 'dokumenti',
    component: DocumentsComponent,
    data: {
      language: 'hr',
      config: {
        en: 'documents'
      }
    }
  },
  {
    path: 'dokumenti/:slug',
    component: DocumentDetailComponent,
    data: {
      language: 'hr',
      config: {
        en: 'documents'
      }
    }
  },
  {
    path: 'events',
    component: EventsComponent,
    data: {
      language: 'en',
      config: {
        hr: 'dogadaji'
      }
    }
  },
  {
    path: 'events/:slug',
    component: EventDetailComponent,
    data: {
      language: 'en',
      config: {
        hr: 'dogadaji'
      }
    }
  },
  {
    path: 'dogadaji',
    component: EventsComponent,
    data: {
      language: 'hr',
      config: {
        en: 'events'
      }
    }
  },
  {
    path: 'dogadaji/:slug',
    component: EventDetailComponent,
    data: {
      language: 'hr',
      config: {
        en: 'events'
      }
    }
  },
  {
    path: 'contact',
    component: ContactComponent,
    data: {
      language: 'en',
      config: {
        hr: 'kontakt'
      }
    }
  },
  {
    path: 'kontakt',
    component: ContactComponent,
    data: {
      language: 'hr',
      config: {
        en: 'contact'
      }
    }
  },
  {
    path: 'news',
    component: NewsComponent,
    data: {
      language: 'en',
      config: {
        hr: 'novosti'
      }
    }
  },
  {
    path: 'news/:slug',
    component: NewsDetailComponent,
    data: {
      language: 'en',
      config: {
        hr: 'novosti'
      }
    }
  },
  {
    path: 'novosti',
    component: NewsComponent,
    data: {
      language: 'hr',
      config: {
        en: 'news'
      }
    }
  },
  {
    path: 'novosti/:slug',
    component: NewsDetailComponent,
    data: {
      language: 'hr',
      config: {
        en: 'news'
      }
    }
  },
  // redirect all other requests to root page
  {
    path: '**',
    redirectTo: '/',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [
    AppContainersModule,
    RouterModule.forRoot(appRoutes),
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }



