import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RecaptchaModule } from 'ng-recaptcha';
import { RecaptchaFormsModule } from 'ng-recaptcha/forms';
import { NgModule, OpaqueToken } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormlyModule, FormlyBootstrapModule } from 'ng-formly';
import { HttpModule, Http } from '@angular/http';
import { FlexLayoutModule } from '@angular/flex-layout';
import { CollapseModule } from 'ng2-bootstrap/collapse';
import { TranslateModule, TranslateLoader, TranslateService } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { Angular2ImageGalleryModule } from 'angular2-image-gallery';

import { FormlyFieldCaptcha } from './plugins/formly/types';

import { HomeComponent, InformationsComponent, DocumentsComponent, EventsComponent, ContactComponent } from './containers';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';

import { AppServicesModule } from './services/app-services.module';
import { AppContainersModule } from './containers/app-containers.module';
import { AppPipesModule } from './pipes/app-pipes.module';
import { AppFormlyModule } from './plugins/formly/app-formly.module';
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: Http) {
    return new TranslateHttpLoader(http);
}

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    RecaptchaModule.forRoot(),
    RecaptchaFormsModule,
    FormsModule,
    ReactiveFormsModule,
    FormlyModule.forRoot({
      types: [{
        name: 'captcha', component: FormlyFieldCaptcha, wrappers: ['fieldset', 'label']
      }]
    }),
    FormlyBootstrapModule,
    HttpModule,
    AppRoutingModule,
    AppContainersModule,
    AppServicesModule,
    AppPipesModule,
    AppFormlyModule,
    FlexLayoutModule,
    Angular2ImageGalleryModule,
    CollapseModule.forRoot(),
    TranslateModule.forRoot({
        loader: {
            provide: TranslateLoader,
            useFactory: HttpLoaderFactory,
            deps: [Http]
        }
    })
  ],
  providers: [
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule { }



