import { Component, OnInit, SimpleChanges, ChangeDetectionStrategy, DoCheck, ChangeDetectorRef } from '@angular/core';
import { BaseContainer } from '../base.container';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { Validators, FormGroup, FormControl } from '@angular/forms';
import { FormlyFieldConfig } from 'ng-formly';
import { environment } from '../../../environments/environment';
import { MailService } from '../../services';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.scss'],
  changeDetection: ChangeDetectionStrategy.Default
})
export class ContactComponent extends BaseContainer implements OnInit, DoCheck {

  static model: any = {};
  // static model: any = {
  //   name: 'Matija Stepanić',
  //   company: 'ITalk d.o.o.',
  //   email: 'stepanic@italk.hr',
  //   subject: 'Ovo je naslov poruka čćšđžŽĐŠĆČ',
  //   message: 'Ovo je poruka čćšđžŽĐŠĆČ\nnovi red\n\ndva nova reda'
  // };

  isInProgress: boolean;
  formState: string;
  isFormValid: boolean;

  form: FormGroup = new FormGroup({});
  formFields: FormlyFieldConfig[];
  model: any = ContactComponent.model;

  /**
   * Read from static model, static has fresh data
   * Write to instance model, instance will update UI
   * static/instance combination is used to keep model data between routes change
   */
  submitForm() {
    // protect multiple parallel requests
    if (this.formState === 'IN_PROGRESS') {
      return;
    }

    this.setFormState('IN_PROGRESS');

    // Read source
    // ContactComponent.model
    const subject = 'IPA INZRAK - Kontakt forma: ' + ContactComponent.model.subject;
    const name = ContactComponent.model.name;
    const company = ContactComponent.model.company;
    const email = ContactComponent.model.email;
    const captcha = ContactComponent.model.captcha;

    const message = `
      <p>Ova poruka je poslana koristeći kontakt formu na web stranici <a href="http//www.ipa-inzrak.info" >www.ipa-inzrak.info</a></p>
      <p><b>Ime i prezime:</b><br>` + name + `</p>
      <p><b>Tvrtka:</b><br>` + (company || '-') + `</p>
      <p><b>Email adresa:</b><br><a href="mailto:` + email + `" >` + email + `</a></p>
      <p><b>Naslov:</b><br>` + (subject || '-') + `</p>
      <p><b>Poruka:</b><br>` + (ContactComponent.model.message.split('\n').join('<br>') || '-') + `</p>
    `;

    // send email
    this.mailService.send(name, email, subject, message, captcha).then((result) => {
      // Write target
      // reset this.model
      this.model = {
        name: '',
        company: '',
        email: '',
        subject: '',
        message: '',
        captcha: ''
      };

      this.setFormState('SUCCESS');
    }, (error) => {
      this.setFormState('ERROR');

      setTimeout(() => {
        this.setFormState('DEFAULT');
      }, 7000);

    });

  }

  /**
   * Change form state and force to detect changes
   * @param formState is state key
   */
  private setFormState(formState: string) {
    this.formState = formState;
    this.chRef.detectChanges();
  }

  constructor(
    protected route: ActivatedRoute,
    protected translate: TranslateService,
    private mailService: MailService,
    private chRef: ChangeDetectorRef
  ) {
    super(route, translate);

    this.isInProgress = false;
    this.isFormValid = true;
    this.formState = 'DEFAULT';

    this.translate.get('CONTACT').subscribe((CONTACT) => {

      this.formFields = [{
        className: 'contact',
        fieldGroup: [
          {
            className: 'name',
            key: 'name',
            type: 'input',
            templateOptions: {
                required: true,
                type: 'text',
                label: CONTACT.FORM.FIELDS.name.label,
                placeholder: CONTACT.FORM.FIELDS.name.placeholder
            },
            validators: {
              required: (control: FormControl) => !Validators.required(control)
            },
            validation: {
              show: true,
              messages: {
                required: CONTACT.FORM.FIELDS.name.validation.required
              },
            },
          },
          {
            className: 'company',
            key: 'company',
            type: 'input',
            templateOptions: {
                type: 'text',
                label: CONTACT.FORM.FIELDS.company.label,
                placeholder: CONTACT.FORM.FIELDS.company.placeholder
            },
            validators: {
            },
            validation: {
              show: true,
              messages: {
              },
            }
          },
          {
            className: 'email',
            key: 'email',
            type: 'input',
            templateOptions: {
                required: true,
                type: 'email',
                label: CONTACT.FORM.FIELDS.email.label,
                placeholder: CONTACT.FORM.FIELDS.email.placeholder
            },
            validators: {
              required: (control: FormControl) => !Validators.required(control),
              email: (control: FormControl) => {
                // tslint:disable-next-line:max-line-length
                return !Validators.pattern(/^(?=.{1,254}$)(?=.{1,64}@)[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+(\.[-!#$%&'*+/0-9=?A-Z^_`a-z{|}~]+)*@[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9])?(\.[A-Za-z0-9]([A-Za-z0-9-]{0,61}[A-Za-z0-9])?)*$/)(control);
              }
            },
            validation: {
              show: true,
              messages: {
                required: CONTACT.FORM.FIELDS.email.validation.required,
                email: CONTACT.FORM.FIELDS.email.validation.email
              },
            }
          },
          {
            className: 'subject',
            key: 'subject',
            type: 'input',
            templateOptions: {
                required: true,
                type: 'text',
                label: CONTACT.FORM.FIELDS.subject.label,
                placeholder: CONTACT.FORM.FIELDS.subject.placeholder
            },
            validators: {
              required: (control: FormControl) => !Validators.required(control)
            },
            validation: {
              show: true,
              messages: {
                required: CONTACT.FORM.FIELDS.subject.validation.required
              },
            }
          },
          {
            className: 'message',
            key: 'message',
            type: 'textarea',
            templateOptions: {
                required: true,
                label: CONTACT.FORM.FIELDS.message.label,
                placeholder: CONTACT.FORM.FIELDS.message.placeholder,
                rows: 6
            },
            validators: {
              required: (control: FormControl) => {
                return !Validators.required(control);
              }
            },
            validation: {
              show: true,
              messages: {
                required: CONTACT.FORM.FIELDS.message.validation.required
              },
            }
          },
          {
            className: 'captcha',
            key: 'captcha',
            type: 'captcha',
            templateOptions: {
                label: CONTACT.FORM.FIELDS.captcha.label,
                siteKey: environment.reCaptcha.siteKey
            },
            validators: {
            },
            validation: {
              show: true,
              messages: {
              },
            }
          },
        ]
      }];

    });
  }

  ngOnInit() {
  }

  ngDoCheck(): void {

    // check every field, if one filed is invalid than whole form is invalid
    if (this.formFields && this.formFields[0] && this.formFields[0].fieldGroup) {
      const group = this.formFields[0].fieldGroup;

      this.isFormValid = true;

      // captcha is special field
      // TODO(stepanic): refactor over validators
      if (!ContactComponent.model.captcha) {
        this.isFormValid = false;
      }

      group.forEach((config: FormlyFieldConfig) => {
        if (config.formControl) {
          // console.log(config.key, config.formControl.valid, config.formControl);
          if (!config.formControl.valid) {
            this.isFormValid = false;
          }
        }
      });

    }
  }



}



