import { Component } from '@angular/core';
import { FieldType } from 'ng-formly';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'formly-field-captcha',
  template: `
    <re-captcha
        [formlyAttributes]="field"
        [(ngModel)]="model[field.key]"
        [ngClass]="{'form-control-danger': valid}"
        name="captcha"
        (resolved)="to.resolved && to.resolved($event)"
        required
        siteKey="{{to.siteKey}}"
    ></re-captcha>
    `,
})
// tslint:disable-next-line:component-class-suffix
export class FormlyFieldCaptcha extends FieldType {


}



