import { Component, OnInit, OnDestroy, ChangeDetectorRef, Input } from '@angular/core';
import { DataService } from '../../services';
import { News } from '../../models';
import { ActivatedRoute } from '@angular/router';
import { TranslateService, LangChangeEvent } from '@ngx-translate/core';
import { Subscription } from 'rxjs/Rx';
import { BaseContainer } from '../base.container';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html',
  styleUrls: ['./news.component.scss']
})
export class NewsComponent extends BaseContainer implements OnInit, OnDestroy {

  @Input()
  howMany: number;

  private subscriptionForLangChange: Subscription;
  public news: News[];

  constructor(
    private dataService: DataService,
    private chRef: ChangeDetectorRef,
    protected translate: TranslateService,
    protected route: ActivatedRoute
  ) {

    super(route, translate);

    this.news = dataService.getNews(translate.currentLang || translate.defaultLang);

    this.subscriptionForLangChange = this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
      this.news = dataService.getNews(event.lang);
    });

  }

  ngOnInit() {
  }

  ngOnDestroy(): void {
    this.subscriptionForLangChange.unsubscribe();
  }
}



