import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormlyModule, FormlyBootstrapModule } from 'ng-formly';
import { FileUploadModule } from 'ng2-file-upload';
import { TranslateModule } from '@ngx-translate/core';
import { RecaptchaModule } from 'ng-recaptcha';
import { RecaptchaFormsModule } from 'ng-recaptcha/forms';
import { MomentModule } from 'angular2-moment';
import { RouterModule } from '@angular/router';
// Angular2ImageGalleryModule is much better gallery component
// import { Ng2ImageGalleryModule } from 'ng2-image-gallery';
import { Angular2ImageGalleryModule } from 'angular2-image-gallery';

import { AppPipesModule } from '../pipes/app-pipes.module';

import {
  HomeComponent,
  InformationsComponent,
  DocumentsComponent,
  EventsComponent,
  ContactComponent,
  NewsComponent
} from '.';
import { InformationsHrComponent } from './informations/i18n/informations-hr/informations-hr.component';
import { InformationsEnComponent } from './informations/i18n/informations-en/informations-en.component';
import { DocumentsHrComponent } from './documents/i18n/documents-hr/documents-hr.component';
import { DocumentsEnComponent } from './documents/i18n/documents-en/documents-en.component';
import { EventsHrComponent } from './events/i18n/events-hr/events-hr.component';
import { EventsEnComponent } from './events/i18n/events-en/events-en.component';
import { NewsDetailComponent } from './news/news-detail/news-detail.component';
import { EventDetailComponent } from './events/event-detail/event-detail.component';
import { DocumentDetailComponent } from './documents/document-detail/document-detail.component';

@NgModule({
  imports: [
    CommonModule,
    AppPipesModule,
    FileUploadModule,
    FormsModule,
    ReactiveFormsModule,
    FormlyModule,
    FormlyBootstrapModule,
    TranslateModule,
    RouterModule,
    RecaptchaModule,
    RecaptchaFormsModule,
    MomentModule,
    // Ng2ImageGalleryModule,
    Angular2ImageGalleryModule
  ],
  declarations: [
    HomeComponent,
    InformationsComponent,
    DocumentsComponent,
    EventsComponent,
    ContactComponent,
    InformationsHrComponent,
    InformationsEnComponent,
    DocumentsHrComponent,
    DocumentsEnComponent,
    EventsHrComponent,
    EventsEnComponent,
    NewsComponent,
    NewsDetailComponent,
    EventDetailComponent,
    DocumentDetailComponent
  ]
})
export class AppContainersModule { }



