import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DataService } from '../../../services';
import { Document } from '../../../models';
import { TranslateService } from '@ngx-translate/core';
import { BaseContainer } from '../../base.container';

export interface GalleryImage {
    thumbnail?: any; //image src for the thumbnail
    image?: any; //image src for the image 
    text?: string; //optional text to show for the image
    [propName: string]: any;
}

@Component({
  selector: 'app-document-detail',
  templateUrl: './document-detail.component.html',
  styleUrls: ['./document-detail.component.scss']
})
export class DocumentDetailComponent extends BaseContainer implements OnInit {

  public item: Document;
  public galleryImages: GalleryImage[];

  constructor(
    protected route: ActivatedRoute,
    protected translate: TranslateService,
    private dataService: DataService
  ) {

    super(route, translate);

    this.galleryImages = new Array<GalleryImage>();

    this.route.params.subscribe((params: any) => {
      console.log(params.slug);
      const slug = params.slug;
      this.item = this.dataService.getDocument(slug);

      console.log('tu sam', this.item, params.slug);

      // Change language depend on news language
      if (this.item && this.item.language) {
        this.translate.use(this.item.language);
      }

      // if (this.item && this.item.images && this.item.images.gallery) {
      //   this.item.images.gallery.forEach(galleryImage => {
      //     this.galleryImages.push({
      //       thumbnail: this.item.images.path + galleryImage.thumbnail,
      //       image: this.item.images.path + galleryImage.image
      //     })
      //   });
      // }
    });

  }

  public isGalleryOpened(event): void {
    console.log('gallery event', event);
  }

  ngOnInit() {
  }

}



